               font-size: 24px;\n\n                        &:hover {\n                            color: #e74c3c;\n                        }\n                    }\n                }\n            }\n\n            .ivu-tabs-tab-active {\n                &:focus,\n                &:active {\n                    border-left: solid 1px #dedede;\n                    border-right: solid 1px #dedede;\n                }\n\n                &:before {\n                    content: '';\n                    display: block; // width: 100%;\n                    height: 2px;\n                    background: #3399ff;\n                    position: absolute;\n                    top: -1px;\n                    left: -1px;\n                    right: -1px;\n                }\n            }\n        }\n\n        .ivu-tabs-tabpane {\n            height: 100%;\n            .crud-config{\n                height: 100%;\n                .crud-table{\n                    height: 100%;\n                }\n            }\n        }\n\n        .ivu-tabs-content {\n            background: #ffffff;\n            height: calc(100% - 30px); // padding: 20px;\n            border-left: solid 1px #dedede;\n            border-right: solid 1px #dedede;\n            border-bottom: solid 1px #dedede;\n        }\n    }\n\n    .form-controls {\n        height: 60px;\n        margin-top: auto;\n    }\n}\n\n.ivu-date-picker, .ivu-upload, .ivu-upload button {\n    width: 100%;\n}\n\n.ivu-upload-select {\n    .ivu-btn {\n        display: flex;\n        flex-direction: column;\n        justify-content: center;\n        align-items: center;\n        padding: 5px;\n\n        img {\n            height: 60px;\n            margin-bottom: 5px;\n        }\n    }\n}\n\n.form-item-register {\n    display: flex !important;\n\n    .select-char {\n        width: 60px;\n\n        .multiselect__tags {\n            border-right: 0 !important;\n            padding: 8px 30px 0 10px !important;\n            height: 32px !important;\n        }\n    }\n\n    .first-char {\n        .multiselect__tags {\n            border-top-right-radius: 0 !important;\n            border-bottom-right-radius: 0 !important;\n        }\n    }\n\n    .second-char {\n        .multiselect__tags {\n            border-radius: 0 !important;\n        }\n    }\n\n    .ivu-input {\n        border-top-left-radius: 0 !important;\n        border-bottom-left-radius: 0 !important;\n    }\n}\n\n/*\n.v-select {\n    .dropdown-toggle {\n        border-color: $vue-select-border;\n        background: #ffffff;\n        min-height: 32px;\n        padding-bottom: 3px;\n        transition: border .2s ease-in-out, background .2s ease-in-out, box-shadow .2s ease-in-out;\n        .vs__selected-options {\n            width: calc(100% - 19px);\n            input {\n                font-size: 12px;\n                font-family: \"Fira Sans\", sans-serif;\n                padding: 0 4px;\n            }\n            input::-webkit-input-placeholder {\n                color: $placeholder-compact;\n            }\n            .selected-tag {\n                display: inline-block;\n                white-space: nowrap;\n                text-overflow: ellipsis;\n                overflow: hidden;\n                padding: 1px 7px;\n                font-size: 12px;\n                color: #515a6e;\n                font-weight: 400 !important;\n            }\n        }\n        .vs__actions {\n            .clear {\n                display: none;\n            }\n\n            .open-indicator {\n                width: 10px;\n            }\n            .open-indicator:before {\n                border-width: 1px 1px 0px 0;\n                height: 7px;\n                width: 7px;\n            }\n        }\n    }\n    .dropdown-toggle:hover, .dropdown-toggle:active, .dropdown-toggle:focus {\n        border: solid 1px $primary-compact !important;\n    }\n    .dropdown-toggle:focus {\n        border-color: $primary-compact;\n        outline: 0;\n\n    }\n 